html {
  scroll-behavior: smooth;
}

body {
  color: #2887d9;
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: #3a3b3d;
  text-decoration: none;
}

.visually-hidden {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  position: absolute !important;
}

li {
  list-style-type: none;
}

input {
  outline: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 390px) {
  .container {
    width: 390px;
  }
}

@media screen and (min-width: 1366px) {
  .container {
    width: 1366px;
  }
}

@media screen and (min-width: 1920px) {
  .container {
    width: 1920px;
  }
}

@media screen and (max-width: 1365px) {
  .logo-div {
    justify-content: space-between;
    align-items: center;
    margin-bottom: 13px;
    display: flex;
  }
}

.header .container {
  position: relative;
}

@media screen and (max-width: 1365px) {
  .burger-menu {
    background-color: #fff;
    padding: 13px 20px 46px;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
  }
}

@media screen and (min-width: 1366px) {
  .burger-menu {
    margin-left: auto;
  }
}

.burger-menu.is-open, .burger-button.is-open .btn-close {
  display: block;
}

.burger-button.is-open .btn-open, .btn-close {
  display: none;
}

@media screen and (min-width: 1366px) {
  .burger-button, .search {
    display: none;
  }
}

.burger-button, .search, .login-language button, .vhid-v-kabinet-btn {
  background-color: #0000;
  border: none;
}

.logo img {
  width: 186px;
  height: 48px;
}

svg {
  fill: #0000;
}

@media screen and (min-width: 1366px) {
  nav {
    display: flex;
  }

  .flex-nav {
    align-items: center;
    display: flex;
  }
}

.login-language {
  border-top: 1px solid #72b7f2;
  border-bottom: 1px solid #72b7f2;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.login-language .cabinet:before, .login-language .global:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
  margin-right: 12px;
  display: inline-block;
}

.login-language .cabinet:before {
  background-image: url("vhіd-v-kabіnet.145bdfce.svg");
}

.login-language .global:before {
  background-image: url("global.0d813efe.svg");
}

.login-language button {
  text-align: center;
  color: #3a3b3d;
  justify-content: center;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  display: flex;
}

.login-language button:hover, .login-language button:focus {
  color: #2887d9;
}

.login-language .eng {
  color: #d0e1f1;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
}

@media screen and (min-width: 1366px) {
